<template>
    <div>
        <Message :show.sync="msgShow" :type="msgType" :msg="msg"/>
        <!-- Â∏ñÂ≠êÂàóË°® -->
        <div class="col-md-9 topics-index main-col">
            <div class="panel panel-default">
                <div class="panel-heading">
                    <ul class="list-inline topic-filter">
                        <li v-for="item in filters">
                            <router-link v-title="item.title" :class="{ active: filter === item.filter }" :to="`/topics?categoryId=${currentCategoryId}&filter=${item.filter}`">{{ item.name }}</router-link>
                        </li>
                    </ul>
                    <div class="clearfix"></div>
                </div>

                <div class="panel-body remove-padding-horizontal">
                    <ul class="list-group row topic-list">
                        <li v-for="article in articles" :key="article.id" class="list-group-item">
                            <router-link :to="`/articles/${article.id}/content`" tag="div" class="reply_count_area hidden-xs pull-right">
                                <div class="count_set">
                                    <span class="count_of_votes" title="ÊäïÁ•®Êï∞">{{ article.like_count ? article.like_count : 0 }}</span>
                                    <span class="count_seperator">/</span>
                                    <span class="count_of_replies" title="ÂõûÂ§çÊï∞">{{ article.reply_count ? article.reply_count : 0 }}</span>
                                    <span class="count_seperator">|</span>
                                    <abbr class="timeago">{{ article.updated_at | moment('from') }}</abbr>
                                </div>
                            </router-link>
                            <router-link :to="`/my/${article.user_id}`" tag="div" class="avatar pull-left">
                                <img :src="article.user.avatar" class="media-object img-thumbnail avatar avatar-middle">
                            </router-link>
                            <router-link :to="`/articles/${article.id}/content`" tag="div" class="infos">
                                <div class="media-heading">
                                    {{ article.title }}
                                </div>
                            </router-link>
                        </li>
                    </ul>
                </div>

                <!-- ÂàÜÈ°µÁªÑ‰ª∂ -->
                <div class="panel-footer text-right remove-padding-horizontal pager-footer">
                    <Pagination :currentPage="currentPage" :total="total" :pageSize="pageSize" :onPageChange="changePage" />
                </div>
            </div>
        </div>
        <!-- ‰æßÊ†è -->
        <TheSidebar/>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    // ÂºïÂÖ• TheSidebar.vue ÁöÑÈªòËÆ§ÂÄº
    import TheSidebar from '@/components/layouts/TheSidebar'
    import { getTopics } from '@/api/topic.js'

    export default {
        name: 'Home',
        components: {
            // Â±ÄÈÉ®Ê≥®ÂÜå TheSidebar
            TheSidebar
        },
        data() {
            return {
                msg: '',
                msgType: '',
                msgShow: false,
                articles: [],
                filter: 'default',
                filters: [
                    { filter: 'default', name: 'Ê¥ªË∑É', title: 'ÊúÄÂêéÂõûÂ§çÊéíÂ∫è'},
                    // { filter: 'excellent', name: 'Á≤æÂçé', title: 'Âè™ÁúãÂä†Á≤æÁöÑËØùÈ¢ò'},
                    // { filter: 'vote', name: 'ÊäïÁ•®', title: 'ÁÇπËµûÊï∞ÊéíÂ∫è'},
                    { filter: 'recent', name: 'ÊúÄËøë', title: 'ÂèëÂ∏ÉÊó∂Èó¥ÊéíÂ∫è'},
                    // { filter: 'noreply', name: 'Èõ∂ÂõûÂ§ç', title: 'Êó†‰∫∫ÈóÆÊ¥•ÁöÑËØùÈ¢ò'}
                ],
                total: 0, // ÊñáÁ´†ÊÄªÊï∞
                pageSize: 20, // ÊØèÈ°µÊù°Êï∞
            }
        },
        beforeRouteEnter(to, from, next) {
            const fromName = from.name
            const logout = to.params.logout
            next(vm => {
                if (vm.$store.state.auth) {
                    switch (fromName) {
                        case 'Register':
                            vm.showMsg('Ê≥®ÂÜåÊàêÂäü')
                            break
                        case 'Login':
                            vm.showMsg('ÁôªÂΩïÊàêÂäü')
                            break
                    }
                } else if (logout) {
                    vm.showMsg('Êìç‰ΩúÊàêÂäü')
                }

                vm.setDataByFilter(to.query.filter)
            })
        },
        created() {
            let params = {
                page: this.currentPage,
                include: 'user,category,likeUser'
            }
            // Ëé∑ÂèñÂΩìÂâçÂàÜÁ±ª
            if (this.currentCategoryId) {
                params['filter[category_id]'] = this.currentCategoryId
            }
            getTopics(params).then((res)=>{
                this.articles = res.data.data
                this.total = res.data.meta.total
            })
        },
        computed: {
            ...mapState([
                'auth',
                'user'
            ]),
            // ÂΩìÂâçÈ°µÔºå‰ªéÊü•ËØ¢ÂèÇÊï∞ page ËøîÂõû
            currentPage() {
                return parseInt(this.$route.query.page) || 1
            },
            // ÂΩìÂâçÂàÜÁ±ª
            currentCategoryId() {
                return parseInt(this.$route.query.categoryId) || 0
            }
        },
        watch: {
            auth(value) {
                if (!value) {
                    this.showMsg('Êìç‰ΩúÊàêÂäü')
                }
            },
            '$route'(to) {
                this.setDataByFilter(to.query.filter)
            },
            currentCategoryId() {
                this.setDataByFilter()
            },
        },
        methods: {
            showMsg(msg, type = 'success') {
                this.msg = msg
                this.msgType = type
                this.msgShow = true
            },
            setDataByFilter(filter = 'default') {
                let params = {
                    page: this.currentPage,
                    include: 'user,category,likeUser'
                }
                // Ëé∑ÂèñÂΩìÂâçÂàÜÁ±ª
                if (this.currentCategoryId) {
                    params['filter[category_id]'] = this.currentCategoryId
                }
                if (filter != 'default') {
                    params['filter[withOrder]'] = filter
                }
                getTopics(params).then((res)=>{
                    this.articles = res.data.data
                    this.total = res.data.meta.total
                })
                // ÊØèÈ°µÊù°Êï∞
                // const pageSize = this.pageSize
                // ÂΩìÂâçÈ°µ
                // const currentPage = this.currentPage
                // ËøáÊª§ÂêéÁöÑÊâÄÊúâÊñáÁ´†
                // const allArticles = this.$store.getters.getArticlesByFilter(filter)
                this.filter = filter
                // ÊñáÁ´†ÊÄªÊï∞
                // this.total = allArticles.length
                // ÂΩìÂâçÈ°µÁöÑÊñáÁ´†
                // this.articles = allArticles.slice(pageSize * (currentPage - 1), pageSize * currentPage)
            },
            // ÂõûË∞ÉÔºåÁªÑ‰ª∂ÁöÑÂΩìÂâçÈ°µÊîπÂèòÊó∂Ë∞ÉÁî®
            changePage(page) {
                // Âú®Êü•ËØ¢ÂèÇÊï∞‰∏≠Ê∑∑ÂÖ• pageÔºåÂπ∂Ë∑≥ËΩ¨Âà∞ËØ•Âú∞ÂùÄ
                // Ê∑∑ÂÖ•ÈÉ®ÂàÜÁ≠â‰ª∑‰∫é Object.assign({}, this.$route.query, { page: page })
                this.$router.push({ query: { ...this.$route.query, page } })
            }
        }
    }
</script>

